<template>
  <div class="p_home p_home">
    <div class="left_content">
      <ul class="label_tab">
        <li
          class="laber_tab_cont"
          v-for="(target, id) in navList"
          :key="id"
          @click="choseprofrom(id)"
        >
          <div class="tab_cont" :class="{ secectBackground: flag == id }">
            {{ target.name }}
          </div>
          <div :class="{ right_arrow: flag == id }"></div>
        </li>
      </ul>
    </div>
    <div class="right_content">
      <div class="search_list">
        <div class="search_baner">
          <img class="search_img" src="../../../assets/images/pcIcon.png" />
          <div class="search_contain">
            <i class="iconfont iconsousuo icon_search"></i>
            <input class="search_input" type="text" placeholder="病症搜索" />
          </div>
          <div class="search_btn" @click="searchBtn">搜索</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      navList: [
        { name: "生理", id: 0 },
        { name: "病理", id: 1 },
        { name: "治法", id: 2 },
        { name: "病症", id: 3 },
        { name: "穴位", id: 4 },
        { name: "经络", id: 5 },
      ],
      flag: 0,
    };
  },
  watch: {},
  computed: {},
  methods: {
    searchBtn() {
      this.$router.push({ name: "searchlist" });
    },
    choseprofrom(id) {
      this.flag = id;
    },
  },
  created() {},
  mounted() {},
};
</script>

<style lang="scss" scoped>
@import "../../../assets/styles/home/index.scss";
@import "../../../assets/styles/currency/index.scss";
</style>


